:root{
  --primary: #20232a;
  --secondary: rgb(40, 44, 52);
  --color-primary: #61dafb;

  --blue-gray: #364f6b;
  --gray: #5f6163;
  --black: #181818;
  --white: #eeeeee;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Bg */
.primary{
  background: var(--primary);
}
.secondary{
  background: var(--secondary);
}
/* Bg */

.navbarContainer {
  display: flex;
  width: 100vw;
  height: 70px;
  background: var(--primary);
  align-items: center;
}

.navbarTitleContainerButton { 
  height: 100%;
  display: flex;
  align-items: center;
  background: var(--primary);
  border: none;
}

.btnCursor {
  cursor: pointer;
}

.navbarIcon{
  width: 40px;
  height: 40px;
  margin-left: 10px;
}

.navbarTitle{
  color: var(--color-primary);
  margin-left: 10px;
  margin-right: 30px;
}

.navbarButtonContainer{
  display: flex;
  width: 100%;
  height: 100%;
}
.navbarButton {
  border: none;
  font-size: 16px;
  background: var(--primary);
  color: var(--gray);
  height: 100%;
  margin: 0 10px;
  padding: 0 20px;
}
.navbarButtonIsActive{
  border-bottom: 3px solid var(--color-primary);
  color: var(--color-primary);
}


.homeHeader{
  height: 26em; 
  background: var(--secondary);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  contain: strict;
}

.homeHeader h1{
  color: var(--color-primary);
  width: 100%;
  text-align: center;
  font-size: 56px;
  margin-bottom: -30px;
  font-weight: 700;
  position: relative;
  z-index: 100;
}
.homeHeader span{
  color: var(--white);
  font-weight: 100;
  width: 100%;
  text-align: center;
  font-size: 26px;
  position: relative;
  z-index: 100;
}

.homeHeaderImg{
  position: absolute;
  width: 36em;
  right: 1em;
  top: 30%;
  opacity: .04;
}

.homeHeaderBtnContainer button{
  padding: 1.2em;
  margin: .5em .2em;
  border: none;
  background: inherit;
  position: relative;
  z-index: 100;
  border-radius: 2px;
}
.homeHeaderBtnContainer button:first-child {
  background: var(--color-primary);
}


